import { ApplicationRef, ComponentFactoryResolver, ComponentRef, EmbeddedViewRef, Inject, Injector, OnDestroy, Type } from '@angular/core';
import { DOCUMENT } from '@angular/common';

export abstract class FabricModal<T> implements OnDestroy {

	componentRef: ComponentRef<T> = null;

	protected constructor(private readonly componentFactoryResolver: ComponentFactoryResolver,
						  private readonly applicationRef: ApplicationRef,
						  private readonly injector: Injector,
						  @Inject(DOCUMENT) private readonly document: Document) {
	}

	ngOnDestroy() {
		this.removeComponent();
	}

	abstract getComponent(): Type<T>

	createAndAppend(): void {
		if (this.componentRef) {
			this.removeComponent();
		}

		const componentRef = this.componentFactoryResolver
								 .resolveComponentFactory(this.getComponent())
								 .create(this.injector);

		componentRef.changeDetectorRef.detectChanges();

		this.applicationRef.attachView(componentRef.hostView);

		const domNotificationContainerElement = (componentRef.hostView as EmbeddedViewRef<any>)
			.rootNodes[0] as HTMLElement;

		this.document.body.appendChild(domNotificationContainerElement);

		this.componentRef = componentRef;
	}

	removeComponent(): void {
		if (this.componentRef) {
			this.applicationRef.detachView(this.componentRef.hostView);
			this.componentRef.destroy();
			this.componentRef = null;
		}
	}

	getComponentRef(): ComponentRef<T> {
		return this.componentRef;
	}
}
