import { ElementRef, Renderer2 } from '@angular/core';

import { Theme } from '../../themes/theme';
import { FabricReactive } from '../../common/fabric-reactive';


export abstract class DialogComponent extends FabricReactive {

	private static readonly CSS_CLASS_PREFIX = 'gui-';

	protected constructor(private readonly elementRef: ElementRef,
						  private readonly renderer: Renderer2,
						  private readonly theme: Theme) {
		super();
	}

	protected addTheme(theme?: Theme): void {

		if (!theme) {
			theme = this.theme;
		}

		this.removeThemes();
		const cssClass = DialogComponent.CSS_CLASS_PREFIX + theme.toLowerCase();

		this.renderer.addClass(this.elementRef.nativeElement, cssClass);
	}

	protected removeThemes(): void {
		const themes = Object.keys(Theme)
							 .map((key: string) => Theme[key].toLowerCase())
							 .filter((val) => !Number.isInteger(val));

		themes.forEach((theme: string) => {
			const cssClass = DialogComponent.CSS_CLASS_PREFIX + theme;
			this.renderer.removeClass(this.elementRef.nativeElement, cssClass);
		});
	}
}
